import React, {useState} from 'react'
import {ReactComponent as Logo} from '../../LOGO.svg'
import {CSSTransition} from 'react-transition-group'


function DropDown () {

    const [activeMenu, setActiveMenu] = useState('main');
    const [menuHeight, setMenuHeight] = useState(null);

    function calcHeight (el) {
        const height = el.offsetHeight;
        setMenuHeight(height);
    }

    function DropDownItem (props) {
        return(
            <a href={props.pathto} className="menu-item" onClick={()=>props.goToMenu && setActiveMenu(props.goToMenu)}>
                <span className="icon-button">{props.leftIcon}</span>
                {props.children}
                <span className="icon-right">{props.rightIcon}</span>
            </a>
        )
    }

    return (
        <div className="dropdown" style={{height: menuHeight}}>
            <CSSTransition in={activeMenu === "main"} 
            unmountOnExit 
            timeout={500} 
            classNames='menu-primary'
            onEnter={calcHeight}>
                <div className="menu">
                     <DropDownItem pathto='/profile'>My Profile</DropDownItem>
                    <DropDownItem 
                        leftIcon={<Logo></Logo>} 
                        goToMenu='emotions'>
                            Emotion Rate
                    </DropDownItem>
                    <DropDownItem pathto='/logout'>Logout</DropDownItem>
                </div>
            </CSSTransition>
            <CSSTransition in={activeMenu === "emotions"} 
            unmountOnExit 
            timeout={500} 
            classNames='menu-secondary'
            onEnter={calcHeight}>
                <div className="menu">
                     <DropDownItem goToMenu='main'>Go back</DropDownItem>
                     <DropDownItem >Mental Intelligence</DropDownItem>
                     <DropDownItem >Physical Intelligence</DropDownItem>
                     <DropDownItem >Spritual Intelligence</DropDownItem>
                </div>
    
            </CSSTransition>
        </div>
    );
}

export default DropDown;